@use "../common/variables.scss";

.catalog {
  padding-top: min(2.7rem, 35px);

  @media (min-width: variables.$tablet) {
    padding-top: min(6.3rem, 63px);
  }

  @media (min-width: variables.$desktop) {
    padding-top: 72px;
  }
}

.catalog__wrapper {
  @media (min-width: variables.$tablet) {
    margin-bottom: min(7rem, 70px);
  }

  @media (min-width: variables.$desktop) {
    margin-bottom: 78px;
  }
}

.catalog__title {
  margin: 0;
  margin-bottom: 41px;

  @media (min-width: variables.$tablet) {
    margin-bottom: min(16.5rem, 165px);
  }

  @media (min-width: variables.$desktop) {
    margin-bottom: 165px;
  }
}
