@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswald-regular.woff2") format("woff2"), url("../fonts/oswald/oswald-regular.woff") format("woff");
  font-display: swap;
  unicode-range: U+0410-044f, U+0000-007f;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/lato-regular.woff2") format("woff2"), url("../fonts/lato/lato-regular.woff") format("woff");
  font-display: swap;
  unicode-range: U+0410-044f, U+0000-007f;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 3.125vw;
}
@media (min-width: 640px) {
  .page {
    font-size: 1.3020833333vw;
  }
}
@media (min-width: 1280px) {
  .page {
    font-size: 0.6944444444vw;
  }
}

.page__body {
  position: relative;
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
  margin: 0;
  background-color: #ffffff;
  color: #000000;
}

.page__background-wrapper {
  min-width: 100%;
}
@media (min-width: 1280px) {
  .page__background-wrapper {
    display: grid;
    grid-template-rows: repeat(3, auto);
  }
}

@media (min-width: 1280px) {
  .page__header {
    grid-column: 1/-1;
    grid-row: 1/2;
  }
}

.page__content {
  min-width: 100%;
}
@media (min-width: 1280px) {
  .page__content--with-background {
    grid-column: 1/-1;
    grid-row: 1/-1;
  }
}

.page__hero {
  max-width: 2200px;
  margin: 0 auto;
}

.container {
  width: calc(100% - 40px);
  margin: 0 auto;
}
@media (min-width: 640px) {
  .container {
    width: calc(100% - 60px);
  }
}
@media (min-width: 1280px) {
  .container {
    width: calc(100% - 220px);
    max-width: 2145px;
  }
}

.academy-logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
}
@media (min-width: 640px) {
  .academy-logo {
    gap: 23px;
    padding: 0;
  }
}

.academy-logo:hover .academy-logo__icon,
.academy-logo:focus .academy-logo__icon {
  color: #68b738;
}

.academy-logo:active {
  opacity: 0.3;
}

.academy-logo__text {
  font-family: "Lato", "Arial", sans-serif;
  font-size: clamp(16px, 1.6rem, 20px);
  line-height: clamp(20px, 2rem, 25px);
  font-weight: 400;
  color: #444444;
}

.academy-logo__icon {
  width: clamp(27px, 2.7rem, 36px);
  height: clamp(34px, 3.4rem, 45px);
  color: #666666;
}

.title {
  font-size: min(3.6rem, 45px);
  font-weight: 400;
}
.title--primary {
  line-height: min(3.6rem, 45px);
}
.title--secondary {
  line-height: min(4rem, 50px);
}
@media (min-width: 640px) {
  .title {
    font-size: min(6rem, 70px);
    line-height: min(6rem, 70px);
  }
}
@media (min-width: 1280px) {
  .title {
    font-size: clamp(60px, 6rem, 70px);
    line-height: clamp(60px, 6rem, 70px);
  }
}

.button {
  display: grid;
  place-content: center;
  margin: 0;
  border: none;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: min(1.6rem, 22px);
  line-height: min(2rem, 28px);
  font-weight: 400;
  text-decoration: none;
  text-transform: uppercase;
  cursor: pointer;
}
@media (min-width: 640px) {
  .button {
    font-size: clamp(18px, 2rem, 24px);
    line-height: clamp(18px, 2rem, 24px);
  }
}
@media (min-width: 1280px) {
  .button {
    font-size: clamp(20px, 2rem, 24px);
    line-height: clamp(20px, 2rem, 24px);
  }
}
.button--primary {
  color: #ffffff;
  background-color: #68b738;
}
.button--primary:hover {
  background-color: #5eaa2f;
}
.button--primary:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5eaa2f;
}
.button--secondary {
  color: #444444;
  background-color: #f2f2f2;
}
.button--secondary:hover {
  background-color: #ebebeb;
}
.button--secondary:active {
  color: rgba(68, 68, 68, 0.3);
  background-color: #ebebeb;
}

.map {
  display: block;
  border: none;
}

.ymaps-2-1-79-map-copyrights-promo {
  visibility: hidden;
}

.ymaps-2-1-79-copyright {
  visibility: hidden;
}

.line-decor {
  position: relative;
}
.line-decor::after {
  content: "";
  position: absolute;
  width: 100vw;
  height: 2px;
  top: 18px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #68b738;
  z-index: -1;
}
@media (min-width: 768px) {
  .line-decor::after {
    top: 22px;
  }
}

.header {
  position: relative;
}

.header-logo {
  text-decoration: none;
}
@media (max-width: 639px) {
  .header-logo {
    width: min(3.3rem, 43px);
    height: min(3.8rem, 50px);
  }
}
@media (min-width: 640px) {
  .header-logo {
    width: clamp(160px, 17.4rem, 240px);
    height: clamp(50px, 5rem, 62px);
    display: flex;
    align-items: center;
    gap: clamp(13px, 1.3rem, 32px);
  }
}
@media (min-width: 1280px) {
  .header-logo {
    min-width: clamp(202px, 20.2rem, 240px);
    height: clamp(59px, 5.9rem, 75px);
  }
}
.header-logo:hover {
  opacity: 0.8;
}
.header-logo:active {
  opacity: 0.6;
}

.header-logo__image {
  width: 100%;
  height: auto;
  object-fit: contain;
}
@media (min-width: 640px) {
  .header-logo__image {
    width: clamp(60px, 6rem, 75px);
  }
}
@media (min-width: 1280px) {
  .header-logo__image {
    width: clamp(70px, 7rem, 85px);
  }
}

.header-logo__text {
  width: clamp(101px, 10.1rem, 126px);
  height: auto;
}
@media (max-width: 639px) {
  .header-logo__text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    fill: #111111;
  }
}
@media (min-width: 640px) {
  .header-logo__text {
    fill: #444444;
  }
}
@media (min-width: 1280px) {
  .header-logo__text {
    width: clamp(118px, 11.8rem, 150px);
  }
}

@media (min-width: 640px) {
  .navigation {
    display: flex;
    justify-content: space-between;
    align-items: start;
    padding-top: 25px;
  }
}
@media (min-width: 1280px) {
  .navigation {
    padding-top: 55px;
  }
}

.navigation__hat {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 13px 0 14px;
}
@media (max-width: 639px) {
  .navigation__hat::after {
    content: "";
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 0;
    width: 100vw;
    height: 1px;
    background-color: #d9d9d9;
  }
}
@media (min-width: 640px) {
  .navigation__hat {
    padding: 0;
  }
}

.navigation__toggle {
  padding: 0;
  width: min(2.4rem, 30px);
  height: min(2.4rem, 30px);
  background-color: transparent;
  background-image: url("../img/sprite.svg#burger-button");
  background-repeat: no-repeat;
  background-size: contain;
  border: none;
  cursor: pointer;
}
@media (min-width: 640px) {
  .navigation__toggle {
    display: none;
  }
}
.navigation__toggle--closed {
  background-image: url("../img/sprite.svg#close-button");
  background-position: center;
  background-size: min(1.8rem, 30px) min(1.8rem, 30px);
}
.navigation__toggle--nojs {
  display: none;
}

@media (max-width: 639px) {
  .navigation__list {
    position: absolute;
    z-index: 2;
    left: 0;
    top: 65px;
    width: 100%;
  }
}
@media (min-width: 640px) {
  .navigation__list {
    max-width: max(45rem, 450px);
  }
}
@media (min-width: 1280px) {
  .navigation__list {
    max-width: max(52.9rem, 529px);
  }
}

.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  text-align: center;
}
.site-list--closed {
  display: none;
}
@media (max-width: 639px) {
  .site-list {
    background-color: #ffffff;
  }
}
@media (min-width: 640px) {
  .site-list {
    display: flex;
    flex-direction: row;
    gap: min(3.4rem, 34px);
    flex-wrap: wrap;
    justify-content: end;
    padding-right: 4px;
  }
}
@media (min-width: 1280px) {
  .site-list {
    gap: 33px;
    padding-right: 2px;
  }
}
@media (max-width: 639px) {
  .site-list--nojs {
    position: static;
    display: block;
  }
}

@media (min-width: 1280px) {
  .site-list--with-background .site-list__link {
    color: #ffffff;
  }
}

@media (min-width: 1280px) {
  .site-list--with-background .site-list__item--current::after {
    background-color: #ffffff;
  }
}

.site-list__item {
  position: relative;
}
@media (max-width: 639px) {
  .site-list__item::after {
    content: "";
    position: absolute;
    width: 100vw;
    height: 1px;
    left: 50%;
    right: 0;
    transform: translateX(-50%);
    bottom: 0;
    background-color: #e6e6e6;
  }
}

@media (min-width: 640px) {
  .site-list__item--current::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 2px;
    height: 2px;
    background-color: #68b738;
  }
}

.site-list__link {
  display: block;
  padding: 22px 22px 24px;
  font-size: min(2rem, 24px);
  line-height: min(2rem, 24px);
  font-weight: 400;
  text-decoration: none;
  text-transform: uppercase;
  color: #000000;
}
@media (min-width: 640px) {
  .site-list__link {
    padding: 14px 0 12px;
    font-size: clamp(16px, 1.8rem, 22px);
    line-height: min(2.4rem, 28px);
  }
}
@media (min-width: 1280px) {
  .site-list__link {
    padding: 20px 0 13px;
    font-size: clamp(20px, 2rem, 24px);
    line-height: clamp(24px, 2.4rem, 30px);
  }
}
.site-list__link:hover {
  opacity: 0.6;
}
.site-list__link:active {
  opacity: 0.3;
}
.site-list__link--current {
  cursor: default;
}
.site-list__link--current:hover, .site-list__link--current:active {
  opacity: 1;
}

.footer {
  background-color: #f2f2f2;
}

.footer__wrapper {
  display: grid;
  gap: min(1rem, 15px);
  padding: min(4.1rem, 50px) 0;
}
@media (min-width: 640px) {
  .footer__wrapper {
    grid-template-columns: repeat(3, 1fr);
    align-items: start;
    grid-auto-flow: row dense;
    padding: min(5.5rem, 70px) 0;
  }
}
@media (min-width: 1280px) {
  .footer__wrapper {
    padding: clamp(68px, 6.8rem, 80px) 0;
  }
}

.footer__logo {
  padding-bottom: min(1.7rem, 25px);
  border-bottom: 1px solid #d9d9d9;
}
@media (min-width: 640px) {
  .footer__logo {
    justify-self: start;
    grid-column: 1/2;
    padding: min(0.5rem, 10px) 0;
    border: none;
  }
}

.footer__social {
  margin-bottom: 10px;
  padding: min(1.2rem, 18px) 0 min(1.8rem, 28px);
  border-bottom: 1px solid #d9d9d9;
}
@media (min-width: 640px) {
  .footer__social {
    max-width: max(180px, 18rem);
    margin: 0;
    justify-self: center;
    grid-column: 2/3;
    padding: clamp(7px, 0.7rem, 12px) 0;
    border: none;
  }
}

@media (max-width: 639px) {
  .footer__copyright {
    width: 280px;
    margin: 0 auto;
  }
}
@media (min-width: 640px) {
  .footer__copyright {
    justify-self: end;
    grid-column: 3/-1;
  }
}

.footer-logo {
  display: grid;
  place-content: center;
}

.footer-logo__text {
  width: clamp(128px, 12.8rem, 140px);
  height: auto;
  fill: #111111;
}

.footer-logo:hover .footer-logo__text,
.footer-logo:focus .footer-logo__text {
  fill: #3e3e3e;
}

.footer-logo:active .footer-logo__text {
  fill: #6b6b6b;
}

.social__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: min(2.4rem, 36px);
  max-width: max(13.2rem, 132px);
  margin: 0 auto;
}

.social-link {
  display: block;
  width: clamp(28px, 2.8rem, 35px);
  height: clamp(22px, 2.2rem, 28px);
  background-color: #666666;
}
.social-link--vk {
  mask: url("../img/sprite.svg#vk") no-repeat center;
}
.social-link--youtube {
  mask: url("../img/sprite.svg#youtube") no-repeat center;
}
.social-link--telegram {
  mask: url("../img/sprite.svg#telegram") no-repeat center;
}
.social-link:hover, .social-link:focus {
  background-color: #68b738;
}
.social-link:active {
  opacity: 0.3;
}

.hero {
  padding-top: min(2.7rem, 40px);
  text-align: center;
  background-image: image-set(url("../img/great-img/cat-hero@2x.webp") type("image/webp") 2x, url("../img/great-img/cat-hero.webp") type("image/webp") 1x, url("../img/great-img/cat-hero@2x.jpg") 2x, url("../img/great-img/cat-hero.jpg") 1x), linear-gradient(90deg, #64a73d 50%, #62a53b 50%), linear-gradient(#62a63b calc(100% - 178px), #ffffff calc(100% - 178px));
  background-repeat: no-repeat;
  background-position: top center;
  background-size: auto calc(100% - 178px), auto calc(100% - 178px), auto;
  color: #ffffff;
}
