  .hero {
    padding-top: min(6.3rem, 70px);
    text-align: left;
    background-color: #ffffff;
    background-image: none;
    color: #000000;
  }
}
@media (min-width: 1280px) {
  .hero {
    padding-top: 143px;
    background-image: image-set(url("../img/great-img/cat-hero-desktop@2x.webp") type("image/webp") 2x, url("../img/great-img/cat-hero-desktop.webp") type("image/webp") 1x, url("../img/great-img/cat-hero-desktop@2x.jpg") 2x, url("../img/great-img/cat-hero-desktop.jpg") 1x), linear-gradient(90deg, #ffffff 50%, #63a63d 50%);
