}
@media (min-width: 1280px) {
  .hero__wrapper {
    min-height: 550px;
    margin-bottom: 82px;
    padding-left: 80px;
  }
}

.hero__title {
  max-width: 25rem;
  margin: 0 auto;
  margin-bottom: min(2.5rem, 40px);
}
@media (min-width: 640px) {
  .hero__title {
    grid-column: 1/2;
    min-width: clamp(440px, 44rem, 660px);
    margin: 0;
    margin-bottom: clamp(40px, 4rem, 48px);
  }
}
@media (min-width: 1280px) {
  .hero__title {
    padding-top: 82px;
  }
}

.hero__slogan {
  margin: 0;
  margin-bottom: min(2.9rem, 42px);
  font-size: min(1.4rem, 18px);
  line-height: min(1.4rem, 18px);
  text-transform: uppercase;
}
@media (min-width: 640px) {
  .hero__slogan {
    grid-column: 1/2;
    max-width: max(460px, 46rem);
    margin-bottom: min(5rem, 60px);
    font-size: clamp(18px, 2rem, 24px);
    line-height: clamp(18px, 2rem, 24px);
  }
