.hero__image {
  display: block;
  width: 100%;
  height: auto;
  object-fit: contain;
}

.hero__button {
  padding: min(1.1rem, 15px) min(4.4rem, 60px);
  font-size: min(1.6rem, 20px);
  line-height: min(1.8rem, 23px);
}
@media (min-width: 640px) {
  .hero__button {
    grid-column: 1/2;
    max-width: clamp(244px, 24.4rem, 325px);
    margin-bottom: 10px;
    padding: clamp(15px, 1.7rem, 20px) clamp(24px, 2.6rem, 32px);
    font-size: clamp(18px, 2rem, 24px);
    line-height: clamp(18px, 2rem, 24px);
  }
}
@media (min-width: 1280px) {
  .hero__button {
    font-size: clamp(20px, 2rem, 24px);
    line-height: clamp(20px, 2rem, 24px);
  }
}

@media (min-width: 640px) {
  .programs__wrapper {
    margin-bottom: clamp(40px, 4.5rem, 55px);
  }
}
@media (min-width: 1280px) {
  .programs__wrapper {
    margin-bottom: clamp(72px, 7.2rem, 80px);
  }
}

.programs__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  gap: min(2rem, 30px);
  padding: min(2rem, 30px) 0;
}
@media (min-width: 640px) {
  .programs__list {
    padding: 0;
    gap: clamp(25px, 3rem, 40px);
  }
}
@media (min-width: 1280px) {
  .programs__list {
    grid-template-columns: repeat(2, 1fr);
    gap: clamp(80px, 8rem, 90px);
  }
}

.program {
  display: grid;
  grid-template-columns: min(6.9rem, 120px) 1fr;
  align-items: start;
  padding: 2.1rem 2rem 1.8rem;
  background-color: #f2f2f2;
}
@media (min-width: 640px) {
  .program {
    grid-template-columns: 38.2rem auto;
    grid-auto-flow: dense;
  }
}
@media (min-width: 768px) and (max-width: 1279px) {
  .program {
    grid-template-columns: max(325px, 39.3rem) auto;
  }
}
@media (min-width: 1280px) {
  .program {
    grid-template-columns: clamp(162px, 16.2rem, 264px) 1fr;
  }
}
.program::before {
  grid-column: 1/2;
  content: "";
  display: block;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
@media (min-width: 640px) and (max-width: 1279px) {
  .program::before {
    grid-column: 2/-1;
    grid-row: span 3;
  }
}
@media (min-width: 640px) and (max-width: 1279px) {
  .program--slim {
    padding: min(7.7rem, 92px) min(6.3rem, 75px) min(5.8rem, 69px) min(5.2rem, 62px);
  }
}
@media (min-width: 1280px) {
  .program--slim {
    padding: clamp(41px, 4.1rem, 55px) clamp(61px, 6.1rem, 82px) clamp(58px, 5.8rem, 78px) clamp(52px, 5.2rem, 70px);
  }
}
.program--pro {
  padding-top: 2.4rem;
}
@media (min-width: 640px) and (max-width: 1279px) {
  .program--pro {
    padding: min(7.7rem, 92px) min(2.9rem, 34px) min(5.8rem, 69px) min(5.2rem, 62px);
  }
}
@media (min-width: 1280px) {
  .program--pro {
    padding: clamp(47px, 4.7rem, 63px) clamp(61px, 6.1rem, 82px) clamp(58px, 5.8rem, 78px) clamp(52px, 5.2rem, 70px);
  }
